import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';

import {products} from '../product';
import {CartService} from '../cart.service'; // ÂåØÂÖ•Ë≥ºÁâ©Ëªä service

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {
  product;

  constructor(
    private route: ActivatedRoute,
    // ActivatedRoute Â∞àÈñÄÁî®ÊñºÁî± Angular Ë∑ØÁî±Âô®ËºâÂÖ•ÁöÑÊØèÂÄãË∑ØÁî±ÂÖÉ‰ª∂
    private cartService: CartService,
    // Ê≥®ÂÖ•Ë≥ºÁâ©Ëªä service
  ) { }

  addToCart(product){
    this.cartService.addToCart(product);
    console.log('Your product has been added to the cart!');
  }

  ngOnInit(): void {
    // Ë®ÇÈñ±Ë∑ØÁî±ÂºïÊï∏Ôºå‰∏¶‰∏îÊ†πÊìö productId Áç≤ÂèñË©≤Áî¢ÂìÅ
    this.route.paramMap.subscribe(params=>{
      this.product = products[+params.get('productId')];
    })
  }

}
