import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms'; // FormBuilder Êèê‰æõË™ûÊ≥ïÁ≥ñÁ∞°Âåñ FormControl„ÄÅFormGroup„ÄÅFormArray ÂØ¶‰æãÁöÑÂª∫Á´ã
import { CartService } from '../cart.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss'],
})
export class CartComponent implements OnInit {
  items;
  checkoutForm; // ÂÑ≤Â≠òË°®ÂñÆÈ°ûÂûã
  constructor(
    private cartService: CartService,
    private formBuilder: FormBuilder
  ) {
    // Âª∫Á´ã FormGroup instance ‰ª•ËíêÈõÜ‰ΩøÁî®ËÄÖÂßìÂêç„ÄÅÂú∞ÂùÄ
    this.checkoutForm = this.formBuilder.group({
      name: '',
      address: '',
    });
  }

  //ËôïÁêÜË°®ÂñÆ
  onSubmit(customerData) {
    this.items = this.cartService.clearCart();
    // Èáç FormGroup
    console.log('reset', customerData)
    this.checkoutForm.reset();
  }
  ngOnInit(): void {
    this.items = this.cartService.getItems();
  }
}
